import React, { useState } from "react";
import styled from "styled-components";
import { useRecoilValue, useSetRecoilState } from "recoil";
import {
  residencyDataState,
  selectedResidencyCoordState,
  selectedResidencyCriteriaState,
  selectedResidencyDataState,
} from "@states/sattlementAtoms";
import { cardOpenState } from "@states/accordionAtoms";
import Icon from "@components/Icon";
import { ICONS } from "@constants/icons";
import { Residency } from "@services/settlementService";

export const Result = () => {
  const setCardOpen = useSetRecoilState(cardOpenState);
  const residencyData = useRecoilValue(residencyDataState);
  //   
  const selectedResidencyCriteria = useRecoilValue(
    selectedResidencyCriteriaState
  );
  const [showCriteria, setShowCriteria] = useState(false);

  const residencyCoordState = useSetRecoilState(selectedResidencyCoordState);
  const selectedResidencyData = useSetRecoilState(selectedResidencyDataState);
  const handleCardClick = (item: Residency, index: number) => {
    residencyCoordState({
      lat: item.latitude,
      lng: item.longitude,
      marker: index === 0 ? "S1" : index === 1 ? "S2" : "S3",
    });
    selectedResidencyData(item);
    setCardOpen(true);
  };

  const distanceMap: { [key: string]: string } = {
    near: "",
    mid: "",
    far: "",
  };

  const facilitiesMap: { [key: string]: string } = {
    studyRoom: "",
    tutorAcademy: "",
    artAcademy: "",
    foreignLanguageAcademy: "",
    musicAcademy: "",
    danceAcademy: "",
    slicedRawFish: "",
    koreanFood: "",
    japaneseFood: "",
    chineseFood: "",
    coffeeShop: "",
    convenienceStore: "",
    crimePreventionFacility: "",
  };

  const infra = [
    { key: "subwayStation", name: "" },
    { key: "publicParking", name: "" },
    { key: "traditionalMarket", name: "" },
    { key: "policeStation", name: "" },
    { key: "kindergarten", name: "" },
    { key: "elementarySchool", name: "" },
    { key: "middleSchool", name: "" },
    { key: "highSchool", name: "" },
    { key: "beach", name: "" },
    { key: "park", name: "" },
  ];

  return (
    <Wrapper>
      <NoticeWrapper>
        <Icon name={ICONS.SEARCH} size={20} />
        <Notice>
            
          <br />
          <ColorText>
            <strong>   3</strong>
          </ColorText>
           .
        </Notice>
      </NoticeWrapper>
      <HoverContentWrapper
        onMouseEnter={() => setShowCriteria(true)}
        onMouseLeave={() => setShowCriteria(false)}
      >
        <HoverContent> </HoverContent>
        {showCriteria && selectedResidencyCriteria && (
          <CriteriaWrapper>
            <CriteriaTitle></CriteriaTitle>
            <SubTitle>   </SubTitle>
            <Table>
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                {Object.entries(selectedResidencyCriteria).map(
                  ([key, value]) => {
                    if (
                      typeof value === "string" &&
                      ["near", "mid", "far"].includes(value)
                    ) {
                      const infraItem = infra.find((item) => item.key === key);
                      const displayName = infraItem ? infraItem.name : key;
                      return (
                        <tr key={key}>
                          <td>{displayName}</td>
                          <td className="checkbox">
                            {value === "near" && <Checkbox checked readOnly />}
                          </td>
                          <td className="checkbox">
                            {value === "mid" && <Checkbox checked readOnly />}
                          </td>
                          <td className="checkbox">
                            {value === "far" && <Checkbox checked readOnly />}
                          </td>
                        </tr>
                      );
                    }
                    return null;
                  }
                )}
              </tbody>
            </Table>
            <SubTitle>    </SubTitle>
            <FacilitiesWrapper>
              {Object.entries(selectedResidencyCriteria).map(([key, value]) => {
                if (typeof value === "boolean" && value === true) {
                  return (
                    <Facility key={key}>{facilitiesMap[key] || key}</Facility>
                  );
                }
                return null;
              })}
            </FacilitiesWrapper>
          </CriteriaWrapper>
        )}
      </HoverContentWrapper>

      {residencyData ? (
        residencyData.map((item, index) => (
          <CardWrapper key={index} onClick={() => handleCardClick(item, index)}>
            <TitleWrapper>
              <Icon
                name={
                  index === 0 ? ICONS.S1 : index === 1 ? ICONS.S2 : ICONS.S3
                }
                color="white"
                hoverColor="white"
              />
              <Title>{item.district}</Title>
            </TitleWrapper>
            <Info>
              <Icon
                name={ICONS.ADDRESS}
                size={14}
                color="white"
                hoverColor="white"
              />
              <p> {item.address}</p>
            </Info>
          </CardWrapper>
        ))
      ) : (
        <div>   .</div>
      )}
    </Wrapper>
  );
};

export default Result;

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 30px;
`;

const NoticeWrapper = styled.div`
  display: flex;
  padding-left: 15px;
  width: 94%;
  padding-bottom: 14px;
`;

const Notice = styled.div`
  font-size: 18px;
  margin-left: 8px;
`;

const ColorText = styled.span`
  color: ${(props) => props.theme.colors.semantic.success};
`;

const CardWrapper = styled.div`
  display: flex;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  box-shadow: ${(props) => props.theme.boxShadow};
  margin: 10px;
  height: 120px;
  width: 90%;
  cursor: pointer;
`;

const TitleWrapper = styled.div`
  display: flex;
  align-items: center;
  padding: 0px 10px;
  padding-bottom: 14px;
`;
const Title = styled.div`
  display: flex;
  align-items: center;
  font-family: ${(props) => props.theme.fonts.pretendard.bold};
  font-size: 20px;
  color: ${(props) => props.theme.colors.primary};
  padding-left: 5px;
`;

const Info = styled.div`
  padding: 0px 10px;
  display: flex;
  p {
    font-size: 13px;
    margin-left: 4px;
    color: #4e4e4e;
  }
`;

const CriteriaTitle = styled.h2`
  font-size: 18px;
  margin-bottom: 15px;
  color: ${(props) => props.theme.colors.primary};
`;

const SubTitle = styled.h3`
  font-size: 16px;
  margin-top: 15px;
  margin-bottom: 10px;
  color: ${(props) => props.theme.colors.semantic.success};
`;

const Table = styled.table`
  width: 90%;
  border-collapse: collapse;
  margin-bottom: 50px;
  font-family: ${(props) => props.theme.fonts.pretendard.regular};
  font-size: 13px;
  th,
  td {
    padding: 5px;
    /* text-align: start; */
  }
  th {
    justify-content: center;
    text-align: center;
  }

  .checkbox {
    text-align: center;
  }
`;

const Checkbox = styled.input.attrs({ type: "checkbox" })`
  appearance: none;
  width: 13px;
  height: 13px;
  border: 2px solid #ddd;
  border-radius: 4px;
  outline: none;
  cursor: pointer;

  &:checked {
    background-color: ${(props) => props.theme.colors.primary};
    border-color: ${(props) => props.theme.colors.primary};
  }
`;

const FacilitiesWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
`;

const Facility = styled.div`
  background-color: ${(props) => props.theme.colors.neutral.white};
  color: ${(props) => props.theme.colors.primary};
  padding: 6px 12px;
  border-radius: 4px;
  font-size: 14px;
`;

const HoverContentWrapper = styled.div`
  position: relative;
  margin-bottom: 20px;
`;

const HoverContent = styled.div`
  padding: 10px 20px;
  background-color: ${(props) => props.theme.colors.neutral.lightGrey};
  color: ${(props) => props.theme.colors.neutral.darkGrey};
  border-radius: 10px;
  font-size: 16px;
  cursor: pointer;
`;

const CriteriaWrapper = styled.div`
  padding: 0 20px;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  width: 380px;
  height: 650px;
  overflow-y: auto;
  background-color: ${(props) => props.theme.colors.neutral.lightGrey};
  border-radius: 10px;
  padding: 20px;
  box-shadow: ${(props) => props.theme.boxShadow};
  margin-top: 10px;
  z-index: 1000;
`;
